.switch {
  display: inline-block;
  width: 30px;
  height: 10px;
  margin: 0 8px;
  background: rgba(64, 102, 226, 0.7);
  border-radius: 15px;
  cursor: pointer;
  position: relative; }
  .switch::before {
    content: "";
    position: absolute;
